.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 30px;
  z-index: 1;

  display: flex;
  justify-content: space-between;
  transition: all 0.5s;
  transition-timing-function: ease-in;
}

.nav__black {
  background-color: #111;
}

.nav__logo {
  position: fixed;
  left: 20px;
  width: 80px;
  object-fit: contain;
}

.nav__avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}

.nav__username {
  text-decoration: none;
  color: #fff;
  font-size: 8px;
  font-weight: 600;
  position: fixed;
  right: 20px;
  margin-top: 33px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
